.container__header {
  background: #ffffff;
  height: 100px;
}

.header-glue {
  position: fixed;
  z-index: 99999;
  width: 100%;
  background: #ffffff;
  box-shadow: 0 5px 15px 0 #838383;
  transition: box-shadow 0.5s;
}

.header-glue header {
  padding: 18px 0;
  transition: all 1s;
}

.header-glue header .header-logo {
  transform: scale(0.8);
}
.header-glue header .header-logo:hover {
  transform: scale(1.1);
}

header {
  padding: 38px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  transition: all 1s;
}

header .header-brand {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

header .header-logo {
  transition: all 0.5s;
  transform: scale(1.2);
}
header .header-logo:hover {
  transition: all 0.5s;
  transform: scale(1.4);
}

header .header-trigger {
  display: none;
}

header .header-navigation {
  transition: all 1s;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
header .header-navigation li {
  display: flex;
  align-items: center;
  padding: 0 15px;
}
header .header-navigation li a {
  color: #000000;
  text-transform: uppercase;
}
header .header-navigation li:hover a {
  color: #10c9c3;
}

@media screen and (max-width: 941.292415125px) {
  .container__header {
    height: auto;
  }
  header {
    flex-direction: column;
    align-items: stretch;
  }
  header .header-trigger {
    display: block;
    border: none;
    background: transparent;
    outline: none;
  }
  header .header-trigger:hover {
    color: #10c9c3;
  }
  header .header-brand {
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
  }
  header .header-navigation {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  header .header-navigation-show {
    display: flex;
    height: 100%;
  }
}
.container__footer {
  background: #141414;
  color: #ffffff;
}
.container__footer footer {
  padding: 100px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: #bcbcbc;
}
.container__footer footer .footer-copyrighted {
  display: flex;
  flex-direction: column;
}
.container__footer footer .footer-copyrighted .footer-copyrighted-inc {
  font-size: 1.125em;
  font-weight: 700;
  padding-bottom: 27px;
  color: #ffffff;
}
.container__footer footer .footer-contacts {
  display: flex;
  flex-direction: column;
  flex-grow: 0.5;
  align-items: center;
}
.container__footer footer .footer-navigation {
  display: flex;
  flex-direction: column;
}
.container__footer footer .footer-social-net {
  display: flex;
  flex-direction: column;
  margin-left: 135px;
}
.container__footer footer a {
  color: #bcbcbc;
}
.container__footer footer li:hover a {
  color: #10c9c3;
}

@media screen and (max-width: 1172.95005px) {
  .container__footer footer .footer-copyrighted .footer-copyrighted-inc {
    padding-bottom: 27px;
  }
  .container__footer footer .footer-contacts {
    flex-grow: 0;
  }
  .container__footer footer .footer-social-net {
    margin-left: 0;
  }
}
@media screen and (max-width: 941.292415125px) {
  .container__footer footer {
    padding: 50px 0;
    flex-direction: column;
    text-align: center;
  }
  .container__footer footer ul {
    padding: 15px 0;
  }
}
