.title {
  font-size: 2em;
  padding-bottom: 32px;
  font-weight: 500;
}

.btn {
  box-sizing: border-box;
  background: #10c9c3;
  border: 1px solid #10c9c3;
  width: 178px;
  height: 52px;
  color: #ffffff;
  text-transform: uppercase;
  outline: none;
  transition: 0.25s all;
}

.btn:hover {
  background: transparent;
  color: #10c9c3;
}

.container__proposal {
  background: #f5f5f5;
}

.proposal {
  display: flex;
  padding: 100px 0;
  flex-direction: column;
  align-items: center;
}
.proposal > p {
  width: 683px;
  text-align: center;
  margin-bottom: 40px;
}
.proposal form {
  margin: 15px;
}
.proposal form .form-name {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.proposal .contact_form {
  display: flex;
  flex-direction: column;
}

.cf_element textarea,
.cf_element input {
  padding: 18px 20px;
  margin: 15px;
  outline-color: #10c9c3;
  border: none;
}

.cf_element > .cf_w1 {
  transition: all 1s;
  width: 683px;
}

.cf_element > .cf_w2 {
  transition: all 1s;
  width: 303.5555555556px;
}

.cf_element > .cf_h1 {
  height: 12px;
}

.cf_element > .cf_h2 {
  height: 24px;
}

.cf_element > .cf_h3 {
  height: 36px;
}

.cf_element > .cf_h4 {
  height: 48px;
}

.cf_element > .cf_h5 {
  height: 60px;
}

.cf_element > .cf_h6 {
  height: 72px;
}

.cf_element > .cf_h7 {
  height: 84px;
}

.cf_element > .cf_h8 {
  height: 96px;
}

.cf_element > .cf_h9 {
  height: 108px;
}

.cf_element > .cf_h10 {
  height: 120px;
}

